import { dictionaries } from 'src/rest'
import { dictionariesActions } from 'src/actions'
import { to } from 'src/lib'

const loadCountries = () => async (dispatch) => {
  const { value } = await to(dictionaries.countries())

  if (value && value.data) {
    dispatch(dictionariesActions.loadCountriesSuccess(value.data))
  } else {
    dispatch(dictionariesActions.loadCountriesFailure())
  }
}

export default loadCountries
