import { dictionaries } from 'src/rest'
import { dictionariesActions } from 'src/actions'
import { to } from 'src/lib'

const loadRegions = () => async (dispatch) => {
  const { value } = await to(dictionaries.regions())
  if (value && value.data) {
    dispatch(dictionariesActions.loadRegionsSuccess(value.data))
  } else {
    dispatch(dictionariesActions.loadRegionsFailure())
  }
}

export default loadRegions
