import { createAction } from 'redux-actions'

const prefix = 'DICTIONARIES:'

export const loadStart = createAction(prefix + 'LOAD_START')
export const loadSuccess = createAction(prefix + 'LOAD_SUCCESS')
export const loadFailure = createAction(prefix + 'LOAD_FAILURE')

export const loadCountriesSuccess = createAction(prefix + 'LOAD_COUNTRIES_SUCCESS')
export const loadCountriesFailure = createAction(prefix + 'LOAD_COUNTRIES_FAILURE')

export const loadRegionsSuccess = createAction(prefix + 'LOAD_REGIONS_SUCCESS')
export const loadRegionsFailure = createAction(prefix + 'LOAD_REGIONS_FAILURE')

export const loadCurrenciesSuccess = createAction(prefix + 'LOAD_CURRENCIES_SUCCESS')
export const loadCurrenciesFailure = createAction(prefix + 'LOAD_CURRENCIES_FAILURE')
