import { handleActions } from 'redux-actions'

import { dictionariesActions } from 'src/actions'

const defaultState = {
  countries: [],
  regions: [],
  currencies: [],
}

const reducersMap = {
  [dictionariesActions.loadCountriesSuccess]: (state, action) => ({
    ...state,
    countries: action.payload,
  }),
  [dictionariesActions.loadRegionsSuccess]: (state, action) => ({
    ...state,
    regions: action.payload,
  }),
  [dictionariesActions.loadCurrenciesSuccess]: (state, action) => ({
    ...state,
    currencies: action.payload,
  }),
}

export default handleActions(reducersMap, defaultState)
