import { dictionaries } from 'src/rest'
import { dictionariesActions } from 'src/actions'
import { to } from 'src/lib'

const loadCurrencies = () => async (dispatch) => {
  const { value } = await to(dictionaries.currencies())
  if (value && value.data) {
    dispatch(dictionariesActions.loadCurrenciesSuccess(value.data))
  } else {
    dispatch(dictionariesActions.loadCurrenciesFailure())
  }
}

export default loadCurrencies
