import React, { memo, useCallback, useMemo, useRef, useState } from 'react'
import cn from 'classnames'
import isEmpty from 'lodash/isEmpty'

import { useOnClickOutside } from 'src/hooks'
import { ReactComponent as DownIcon } from 'src/assets/images/icons/down.svg'
import { ReactComponent as CheckboxIcon } from 'src/assets/images/icons/checkbox.svg'
import { ReactComponent as RemoveIcon } from 'src/assets/images/icons/remove.svg'
import { ReactComponent as SearchIcon } from 'src/assets/images/icons/search.svg'

import styles from './styles.module.scss'

const DropdownRaw = (props) => {
  const {
    options,
    value,
    defaultValue = [],
    placeholder,
    hasSearch = false,
    isSimple = false,
    hasError,
    onChange,
    placeholderSearch,
  } = props

  const dropdownRef = useRef(null)
  const [selectOptions, setSelectOptions] = useState(value || defaultValue)
  const [searchValue, setSearchValue] = useState('')
  const [isOpened, setIsOpened] = useState(false)

  useOnClickOutside(dropdownRef, () => {
    if (isOpened) {
      setSearchValue('')
      setIsOpened(false)
      onChange(selectOptions)
    }
  })

  const onSelectValue = useCallback((options) => {
    setSelectOptions(options)
  }, [])

  const onClickOption = useCallback(
    (option, isSelected) => {
      if (isSelected) {
        const nextOption = selectOptions.filter((item) => {
          return item.id !== option.id
        })
        if (isSimple) {
          onSelectValue([])
          setIsOpened(false)
        } else {
          onSelectValue(nextOption)
        }
      } else {
        if (isSimple) {
          onSelectValue([option])
          setIsOpened(false)
        } else {
          onSelectValue([...selectOptions, option])
        }
      }
    },
    [onSelectValue, selectOptions, isSimple],
  )

  const onClickRemove = useCallback(
    (e, option) => {
      e.preventDefault()
      e.stopPropagation()
      onClickOption(option, true)
    },
    [onClickOption],
  )

  const onClickHeader = useCallback(() => {
    if (!isOpened) {
      setIsOpened(true)
    }
  }, [isOpened, setIsOpened])

  const onClickArrow = useCallback(() => {
    if (isOpened) {
      setIsOpened(false)
      onChange(selectOptions)
    }
  }, [isOpened, setIsOpened, onChange, selectOptions])

  const onChangeSearch = useCallback(
    (e) => {
      setSearchValue(e.target.value)
    },
    [setSearchValue],
  )

  const onClickClear = useCallback(() => {
    setSelectOptions([])
  }, [])

  const renderDropDownValue = useMemo(() => {
    if (!isEmpty(selectOptions)) {
      if (isSimple) {
        const option = selectOptions[0]
        return (
          <div className={styles.headerItemSimple} key={option.id}>
            {option && option.label}
          </div>
        )
      }

      return (
        <div className={styles.headerItems}>
          {selectOptions.slice(0, 2).map((option) => {
            return (
              <div className={styles.headerItem} key={option.id}>
                <div className={styles.headerItemValue}>{option && option.label}</div>
                <div className={styles.headerItemRemove}>
                  <RemoveIcon onClick={(e) => onClickRemove(e, option)} />
                </div>
              </div>
            )
          })}
        </div>
      )
    }

    return <div className={styles.headerPlaceholder}>{placeholder}</div>
  }, [selectOptions, placeholder, onClickRemove, isSimple])

  const renderDropDownHeader = useMemo(() => {
    if (isSimple) {
      return (
        <div
          className={cn(styles.header, {
            [styles.headerError]: hasError,
            [styles.headerSimple]: isSimple,
          })}
          onClick={onClickHeader}
        >
          {renderDropDownValue}

          <button
            className={cn(styles.arrow, {
              [styles.opened]: isOpened,
            })}
            onClick={onClickArrow}
            type="button"
          >
            <DownIcon className={styles.icon} />
          </button>
        </div>
      )
    }
    if (hasSearch && isOpened) {
      return (
        <div
          className={cn(styles.header, {
            [styles.headerError]: hasError,
            [styles.headerSearch]: hasSearch,
          })}
          onClick={onClickHeader}
        >
          <button
            className={cn(styles.search, {
              [styles.opened]: isOpened,
            })}
            onClick={onClickArrow}
            type="button"
          >
            <SearchIcon />
          </button>
          <input type="text" className={styles.inputSearch} placeholder={placeholderSearch} onChange={onChangeSearch} />
          <button
            className={cn(styles.arrow, {
              [styles.opened]: isOpened,
            })}
            onClick={onClickArrow}
            type="button"
          >
            <DownIcon className={styles.icon} />
          </button>
        </div>
      )
    }

    const counter = selectOptions.length - 2

    return (
      <div
        className={cn(styles.header, {
          [styles.headerError]: hasError,
        })}
        onClick={onClickHeader}
      >
        {renderDropDownValue}

        {counter > 0 && <div className={styles.counter}>{`+ ${counter}`}</div>}

        <button
          className={cn(styles.arrow, {
            [styles.opened]: isOpened,
          })}
          onClick={onClickArrow}
          type="button"
        >
          <DownIcon className={styles.icon} />
        </button>
      </div>
    )
  }, [
    isOpened,
    onClickHeader,
    renderDropDownValue,
    selectOptions,
    hasError,
    hasSearch,
    onChangeSearch,
    onClickArrow,
    placeholderSearch,
    isSimple,
  ])

  const renderDropDownBox = useMemo(() => {
    const filteredOptions = options.filter((elem) => {
      if (elem && elem.label) {
        return `${elem.label}`.toUpperCase().includes(searchValue.toUpperCase())
      }
      return true
    })

    const currOptions = hasSearch ? filteredOptions : options

    return (
      <div className={styles.box}>
        {isEmpty(currOptions) ? (
          <div className={styles.empty}>Not found</div>
        ) : (
          <ul className={styles.list}>
            {currOptions.map((option) => {
              const { label, id } = option
              const isSelected = selectOptions.find((item) => {
                return item.id === id
              })

              return (
                <li
                  key={id}
                  className={cn(styles.item, {
                    [styles.itemSelected]: isSelected,
                    [styles.itemSimple]: isSimple,
                  })}
                >
                  <button
                    className={cn(styles.option, {
                      [styles.optionSelected]: isSelected,
                      [styles.optionSimple]: isSimple,
                    })}
                    onClick={() => onClickOption(option, isSelected)}
                  >
                    {!isSimple && <div className={styles.status}>{isSelected && <CheckboxIcon />}</div>}
                    <div className={styles.label}>{label}</div>
                  </button>
                </li>
              )
            })}
          </ul>
        )}

        {!isSimple && !isEmpty(selectOptions) && (
          <div className={styles.footer}>
            <button className={styles.clear} onClick={() => onClickClear()}>
              {`Clear all (${selectOptions.length})`}
            </button>
          </div>
        )}
      </div>
    )
  }, [options, selectOptions, onClickOption, hasSearch, searchValue, onClickClear, isSimple])

  const renderDropDown = useMemo(() => {
    return (
      <div
        ref={dropdownRef}
        className={cn(styles.dropdown, {
          [styles.dropdownOpend]: isOpened,
        })}
      >
        {renderDropDownHeader}
        {isOpened && renderDropDownBox}
      </div>
    )
  }, [renderDropDownHeader, renderDropDownBox, isOpened])

  if (options) {
    return renderDropDown
  }

  return null
}

export const Dropdown = DropdownRaw
export default memo(Dropdown)
