import { instance } from '../axiosInstance'

export const countries = () => {
  return instance({
    method: 'get',
    url: `/dictionary/countries`,
  })
}

export const regions = () => {
  return instance({
    method: 'get',
    url: `/dictionary/regions`,
  })
}

export const currencies = () => {
  return instance({
    method: 'get',
    url: `/dictionary/currencies`,
  })
}
