import React, { useRef, useCallback, memo } from 'react'
import PropTypes from 'prop-types'
import cn from 'classnames'

import styles from './styles.module.scss'

const TextareaRaw = (props) => {
  const {
    value,
    label,
    style,
    id,
    name,
    disabled,
    maxLength,
    hasError,
    onChange,
    onFocus,
    onBlur,
    ...textareaProps
  } = props
  const textareaRef = useRef(null)

  const onTextareaChange = useCallback(
    (event) => {
      const value = event.target.value
      onChange && onChange(value)
    },
    [onChange],
  )

  const onTextareaFocus = useCallback(() => {
    onFocus && onFocus()
  }, [onFocus])

  const onTextareaBlur = useCallback(() => {
    onBlur && onBlur()
  }, [onBlur])

  return (
    <div className={styles.textareaWrap}>
      {label && (
        <label
          className={cn(styles.label, {
            [styles.labelError]: hasError,
            [styles.labelDisabled]: disabled,
          })}
          htmlFor={id || name}
        >
          {label}
        </label>
      )}
      <textarea
        {...textareaProps}
        ref={textareaRef}
        disabled={disabled}
        value={value}
        name={name}
        id={id || name}
        maxLength={maxLength}
        onChange={onTextareaChange}
        onFocus={onTextareaFocus}
        onBlur={onTextareaBlur}
        className={cn(styles.textarea, style, {
          [styles.textareaError]: hasError,
        })}
      />
    </div>
  )
}

TextareaRaw.defaultProps = {
  hasError: false,
  disabled: false,
  maxLength: 255,
  value: '',
}

TextareaRaw.propTypes = {
  name: PropTypes.string,
  label: PropTypes.string,
  id: PropTypes.string,
  hasError: PropTypes.bool,
  disabled: PropTypes.bool,
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
  onBlur: PropTypes.func,
  style: PropTypes.string,
}

const Textarea = TextareaRaw
export default memo(Textarea)
