import React, { memo, useState } from 'react'
import cn from 'classnames'

import BaseButton from 'src/components/BaseButton'
import Dropdown from 'src/forms/Dropdown'
import Input from 'src/forms/Input'
import Textarea from 'src/forms/Textarea'

import Modal from '../Modal'
import styles from './styles.module.scss'

const presets = [
  {
    id: 'Value_1',
    value: 'Value_1',
    label: 'Value 1',
  },
  {
    id: 'Value_2',
    value: 'Value_2',
    label: 'Value 2',
  },
  {
    id: 'Value_3',
    value: 'Value_3',
    label: 'Value 3',
  },
  {
    id: 'Value_4',
    value: 'Value_4',
    label: 'Value 4',
  },
  {
    id: 'Value_5',
    value: 'Value_5',
    label: 'Value 5',
  },
  {
    id: 'Value_6',
    value: 'Value_6',
    label: 'Value 6',
  },
  {
    id: 'Value_7',
    value: 'Value_7',
    label: 'Value 7',
  },
]

const AddPresetRaw = (props) => {
  const { onClose, presetName } = props
  const [value, setValue] = useState('')

  const onChangePreset = (value) => {
    setValue(value)
    console.log('onChangePreset', value)
  }

  const onClickBack = () => {
    onClose()
    console.log('onClickBack')
  }

  const onClickAdd = () => {
    console.log('onClickAdd')
  }

  const onChangeInput = () => {
    console.log('onChangeInput')
  }

  return (
    <Modal title="Add custom metric" onClose={onClose} dialogStyles={styles.dialog}>
      <div className={styles.content}>
        <div className={styles.row}>
          <div className={cn(styles.col, styles.col7)}>
            <Input
              name={presetName}
              value={value}
              onChange={(value) => onChangeInput(value)}
              label="Metric name"
              placeholder="Enter the metric name"
            />
          </div>
          <div className={cn(styles.col, styles.col5)}>
            <div className={styles.label}>Metric format</div>
            <Dropdown
              name="Preset"
              onChange={(value) => onChangePreset(value)}
              key="Preset"
              placeholder="Preset name"
              options={presets}
              hasError={false}
              isSimple
            />
          </div>
        </div>
        <div className={styles.row}>
          <div className={cn(styles.col, styles.col12)}>
            <Input
              name={presetName}
              value={value}
              onChange={(value) => onChangeInput(value)}
              label="Description (optional)"
              placeholder="Enter the preset name"
            />
          </div>
        </div>
        <div className={styles.row}>
          <div className={cn(styles.col, styles.col12)}>
            <Textarea name={presetName} value={value} onChange={(value) => onChangeInput(value)} />
          </div>
        </div>
      </div>
      <div className={styles.action}>
        <div className={styles.actionLeft}>
          <BaseButton text="Back" skin="white" disabled={false} onClick={onClickBack} />
        </div>

        <div className={styles.actionRight}>
          <BaseButton text="Add metric" skin="yellow" disabled={false} onClick={onClickAdd} />
        </div>
      </div>
    </Modal>
  )
}

export const AddPreset = memo(AddPresetRaw)
export default AddPreset
